import { useLocation, useNavigate } from 'react-router-dom';
import 'assets/css/rentalHistoryDetail.css';

function Detail() {
  const navigate = useNavigate();
  const location = useLocation();
  const row = location.state;
  const historyBack = () => {
    navigate(-1);
  };

  return (
    <div className="section__grid__wrap content rental__history__detail">
      {/* sub_nav */}
      <div className="sub__nav">
        <div className="left">
          <button onClick={historyBack}>
            <svg viewBox="0 0 24 24" stroke="white">
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M11 17l-5-5m0 0l5-5m-5 5h12"
              />
            </svg>
          </button>
          <dl>
            <dt className="f__medium">대여이력 상세</dt>
          </dl>
        </div>
      </div>
      {/* // sub_nav */}
      {/* 디바이스 ID */}
      <section className="id__wrap">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">디바이스ID</p>
          </div>
          <input defaultValue={row.deviceId} className="custom__input" readOnly />
        </div>
      </section>
      {/* // 디바이스 ID */}
      {/* Transaction ID */}
      <section className="id__wrap">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">Transaction ID</p>
          </div>
          <input type="text" defaultValue={row.transaction} className="custom__input" readOnly />
        </div>
      </section>
      {/* // Transaction ID */}
      {/* 디바이스명 */}
      <section className="device__name grid1">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">디바이스명</p>
          </div>
          <input type="text" defaultValue="을지로3가" className="custom__input" />
        </div>
      </section>
      {/* // 디바이스명 */}
      {/* 사용자 인증 */}
      <section className="user__certification grid1">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">사용자 인증</p>
          </div>
          <div className="list__wrap">
            <ul>
              <li>
                <dl>
                  <dt>성공일시</dt>
                  <dd>
                    <input
                      type="text"
                      className="custom__input"
                      defaultValue="2022-12-01 11:00:11"
                    />
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>사용자명</dt>
                  <dd>
                    <input type="text" className="custom__input" defaultValue="김길동" />
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>사용포인트</dt>
                  <dd>
                    <input type="number" className="custom__input" defaultValue="50" />
                  </dd>
                </dl>
              </li>
            </ul>
          </div>
        </div>
      </section>
      {/* // 사용자 인증 */}
      {/* 배터리 반납 */}
      <section className="battery grid1">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">배터리 반납</p>
          </div>
          <div className="info__wrap">
            <dl>
              <dt>반납일시</dt>
              <dd>
                <input type="text" className="custom__input" defaultValue="2022-12-01 11:00:11" />
              </dd>
            </dl>
            <div>
              <p className="suc">
                <span className="f__medium">성공</span>
              </p>
              <ul>
                <li>
                  <p>1</p>
                  <dl>
                    <dt>슬롯번호</dt>
                    <dd>
                      <input type="number" className="custom__input" defaultValue="1" />
                    </dd>
                  </dl>
                  <dl>
                    <dt>배터리 고유변호</dt>
                    <dd>
                      <input type="number" className="custom__input" defaultValue="12453" />
                    </dd>
                  </dl>
                  <dl>
                    <dt>SoC</dt>
                    <dd>
                      <input type="number" className="custom__input" defaultValue="231" />
                      <span>%</span>
                    </dd>
                  </dl>
                </li>
                <li>
                  <p>2</p>
                  <dl>
                    <dt>슬롯번호</dt>
                    <dd>
                      <input type="number" className="custom__input" defaultValue="2" />
                    </dd>
                  </dl>
                  <dl>
                    <dt>배터리 고유변호</dt>
                    <dd>
                      <input type="number" className="custom__input" defaultValue="34415" />
                    </dd>
                  </dl>
                  <dl>
                    <dt>SoC</dt>
                    <dd>
                      <input type="number" className="custom__input" defaultValue="20" />
                      <span>%</span>
                    </dd>
                  </dl>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </section>
      {/* // 배터리 반납 */}
      {/* 배터리 대여 */}
      <section className="battery grid1">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">배터리 대여</p>
          </div>
          <div className="info__wrap">
            <dl>
              <dt>반납일시</dt>
              <dd>
                <input type="text" className="custom__input" defaultValue="2022-12-01 11:00:11" />
              </dd>
            </dl>
            <div>
              <p className="fail">
                <span className="f__medium">실패</span>
              </p>
              <ul>
                <li>
                  <p>1</p>
                  <dl>
                    <dt>슬롯번호</dt>
                    <dd>
                      <input type="number" className="custom__input" defaultValue="3" />
                    </dd>
                  </dl>
                  <dl>
                    <dt>배터리 고유변호</dt>
                    <dd>
                      <input type="number" className="custom__input" defaultValue="12453" />
                    </dd>
                  </dl>
                  <dl>
                    <dt>SoC</dt>
                    <dd>
                      <input type="number" className="custom__input" defaultValue="100" />
                      <span>%</span>
                    </dd>
                  </dl>
                </li>
                <li>
                  <p>2</p>
                  <dl>
                    <dt>슬롯번호</dt>
                    <dd>
                      <input type="number" className="custom__input" defaultValue="4" />
                    </dd>
                  </dl>
                  <dl>
                    <dt>배터리 고유변호</dt>
                    <dd>
                      <input type="number" className="custom__input" defaultValue="34415" />
                    </dd>
                  </dl>
                  <dl>
                    <dt>SoC</dt>
                    <dd>
                      <input type="number" className="custom__input" defaultValue="100" />
                      <span>%</span>
                    </dd>
                  </dl>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </section>
      {/* // 배터리 대여 */}
      {/* 오류정보 */}
      <section className="error__info grid1">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">오류정보</p>
          </div>
          <div className="list__wrap">
            <ul>
              <li>
                <dl>
                  <dt>발생일시</dt>
                  <dd>
                    <input
                      type="text"
                      className="custom__input"
                      defaultValue="2022-12-01 11:00:11"
                    />
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>오류명</dt>
                  <dd>
                    <input type="text" className="custom__input" defaultValue="도어" />
                  </dd>
                </dl>
              </li>
            </ul>
            <dl>
              <dt>오류메시지</dt>
              <dd>
                <input type="text" className="custom__input" defaultValue="도 닫기 오류" />
              </dd>
            </dl>
          </div>
        </div>
      </section>
      {/* // 오류정보 */}
    </div>
  );
}

export default Detail;
